import React, {Component} from 'react';
import SkillsBar from './SkillsBar.js';

class SkillsDetalle extends Component {
    constructor(props){
        super(props);
        this.state = {            
            mNombre : this.props.data[0].nombre,
            mItems : this.props.data[1].items
        };        
    }

    render(){

        return(

            <div>
                <h5 class="content_skills_title">
                    {this.state.mNombre}
                </h5>
                <div>
                    <ul>
                        {this.state.mItems.map(
                            data => <SkillsBar data={data}/>
                        )}
                    </ul>
                </div> 
                <br/>
            </div>
            
        );
    }



}

export default SkillsDetalle;