import React, {Component} from 'react';
import { Col, Container,  Row } from 'react-bootstrap';
import { Table } from 'react-bootstrap';

class ContentDetalle extends Component{
    constructor(props){
        super(props);
        this.state = {
            mColumnas: this.props.data
        }
        console.log(this.props.data)
        console.log(this.state.mColumnas)
    }

    render(){

        let lColumnas = [];

        for(var i = 1; i < this.state.mColumnas.length;i++){
            lColumnas.push(this.state.mColumnas[i]);
        }


        return(
            <tr>
                {lColumnas.map( (columna) => <td>{columna}</td>) }
            </tr>                
        );
    }
}

export default ContentDetalle;