import React, {Component} from 'react';

class XpDetalle extends Component{
    constructor(props){
        super(props);
        this.state = {
            mEmpresa : this.props.data.empresa,
            mPeriodo : this.props.data.periodo,
            mRol : this.props.data.rol,
            mDescripcion : this.props.data.descripcion,
            mPar : this.props.par,
            mIndice: this.props.indice,
            mIsOvering: false    
        }        
    
        console.log("XDDETALLE");
        console.log(this.props.data);
        console.log(this.props.par);

    }

    mouseHoverHandler = (event) => {
        this.props.onMouseHoverHandler(event)
    }

    mouseOutHandler = (event) => {
        this.props.onMouseOutHandler(event)
    }

    


    render(){

        let esPar = this.state.mIndice % 2;

        if (esPar > 0){
            this.li_style = "li content_xp_li_impar";
            this.timeline_style = "content_xp_timeline_impar";
            this.element_style = "content_xp_element_impar";
        }else{
            this.li_style = "li content_xp_li_par";
            this.timeline_style = "content_xp_timeline_par";
            this.element_style = "content_xp_element_par";
        }

        this.li_style = "li content_xp_li_par";
        this.timeline_style = "content_xp_timeline_par";
        this.element_style = "content_xp_element_par";
        
        return(            
            <li class={this.li_style} onMouseOver={this.mouseHoverHandler} >
                <div class={this.timeline_style}>
                    <h6><span class="span content_xp_element_span">{this.state.mPeriodo}</span></h6>                                                                                            
                </div> 
                <div class="content_xp_element_container" >
                    <div class={this.element_style}>
                                    <div class="content_xp_element_periodo"></div>                
                                    <h5 class="content_xp_element_empresa">{this.state.mEmpresa}</h5>
                                    <h5 class="content_xp_element_rol">{this.state.mRol}</h5>
                                    <h6 class="content_xp_element_descripcion">{this.state.mDescripcion}</h6>
                    </div>                           
                </div>
            </li>            
        );
    }
}

export default XpDetalle;

