import React, {Component} from 'react';
import SkillsDetalle from '../../page/skills/SkillsDetalle.js';
import icono from '@material-ui/icons/Assignment';
import SvgIcon from '@material-ui/core/SvgIcon';
import './Skills.css';
import SkillsBar from './SkillsBar.js';

class SkillsHome extends Component {
    constructor(props){
        super(props);
        this.state = {
            mError : null,
            mLoaded : false,
            mList:[],
            mList2:[]
        }

    }

    render(){

        if(this.state.mError){
            return (
                <div class="content_active_element content_skills">
                    Error:{this.state.mError.message};
                </div>
            )
        }
        else if(!this.state.mLoaded){
            return(
                <div class="content_active_element content_skills">
                    <label>Cargando</label>
                </div>
            )
        }else{
            return(
                <div id="section_skills" class="content_active_element content_skills">                    
                     <h2 class="content_active_element_title"> Conocimientos</h2>  
                     <br/>
                     <br/>                    
                     <div>
                        <div>
                            <ul>
                                {this.state.mList2.areas.map( data => <SkillsDetalle data={data}/> )}                                
                            </ul>
                        </div> 
                     </div>
                </div> 
            )
        }
    }


    componentDidMount(){
        
        var listLocal = [];
        var data = require('../../data/conocimientos.json');
        listLocal = data.rows;

        var listLocal2 = [];
        var data2 = require('../../data/conocimientos2.json');
        listLocal2 =data2;


        this.setState({
            mLoaded: true,
            mList: listLocal,
            mList2: listLocal2
        })
        console.log(data2);
        console.log(data2.areas[0][1].items);
        console.log(listLocal2);
        //console.log(data2.nombre);
        //console.log(data2.items[0].nombre);
        //console.log(data2.items[0].valor);
    }

}

export default SkillsHome;

