import React, {Component} from 'react';
import { CardDeck, Col, Container,  Row } from 'react-bootstrap';
import FormacionCard from './FormacionCard';
import SvgIcon from '@material-ui/core/SvgIcon';
import iconoSchool from '@material-ui/icons/School';
import iconoRecordVoiceOverIcon from '@material-ui/icons/RecordVoiceOver';
import iconoBeenHere from '@material-ui/icons/Stars';

class FormacionDetalle extends Component {

    constructor(props){
        super(props);
        this.state = {
            mNombre : this.props.data.nombre,
            mItems : this.props.data.items,
            mFilas : null
        }              

        this.procesarFormacion = this.procesarFormacion.bind(this);

    }

    componentDidMount(){

        let tempFilas = this.procesarFormacion(this.state.mItems);

        this.setState({mFilas : tempFilas});
        
    }

    render(){

        let iconoTemp = null;

        if ( this.state.mNombre === 'Educación'){
            iconoTemp = iconoSchool;
        }else{
            if ( this.state.mNombre === 'Idiomas'){
                iconoTemp = iconoRecordVoiceOverIcon;
            }else{
                iconoTemp = iconoBeenHere;
            }
        }

        var total = this.state.mItems.length;
        var resto = total % 3;
        var filas = Math.floor(total/3);
        if ( resto > 0){
            filas = filas + 1;
        }
        let cards = new Array(total);
        for (var i = 0; i < filas ; i++){
            for ( var j = 0; j<3 ; j++){

                cards.push ( i , <div><p>"Hola"</p></div> );

            }            
        }



        return(
                <div>       
                    <p class="h5"><SvgIcon component={iconoTemp}/> {this.state.mNombre}</p>                   
                    <div>
                        <ul>
                            {this.state.mFilas}
                        </ul>
                    </div>
                    <br/>
                </div>
        );
    }


    procesarFormacion(items){

        var total = items.length;
        var temp = total;
        var filas = new Array();
        var columnas = new Array();

        
      /*  
        var i = 0;
        var j = 0;

        while (i < total){
            
            columnas = [];
            
            while (j<3){

                if (items[i+j]){
                    columnas.push(  <FormacionCard nombre={this.state.mNombre} data={items[i+j]}/> )
                }

                j++;

            }

            filas.push( 
                <li class ="li formacion_li"> 
                    <div class= "formacion_li_fila">
                        <p>{columnas}</p>
                    </div>
                </li>);

            i = i + j

        }
        */

        var i = 0;
        var j = 0;

        while (i < total){
            columnas.push(  <FormacionCard nombre={this.state.mNombre} data={items[i]}/> )
            i++;
            j++;

            if ( j >= 3 || ( i == total && total < 3 )){
                filas.push( 
                    <li class ="li formacion_li"> 
                        <div class= "formacion_li_fila">
                            <p>{columnas}</p>
                        </div>
                    </li>);
                j = 0;
                columnas = [];
            }


        }


        return filas;

/*
        var final = false;

        console.log(total);

        for ( var i = 0 ; i < total && final === false ; i++ ){

            columnas = [];

            for ( var j = 0 ; j < 3 ; j++ ){        
                if (items[i+j]){
                   columnas.push(  <FormacionCard nombre={this.state.mNombre} data={items[i+j]}/> )
                }else{

                    final = true;

                }
                
            }
            
            filas.push( 
                <li class ="li formacion_li"> 
                    <div class= "formacion_li_fila">
                        <p>{columnas}</p>
                    </div>
                </li>);
                        
        }

        return filas;
*/
    }    

}

export default FormacionDetalle;

