import { Card } from "react-bootstrap";
import './Formacion.css';

const { Component } = require("react");

class FormacionCard extends Component{
    constructor(props){
        super(props);
        this.state = {
            mNombre : this.props.nombre,
            mTitulo : this.props.data.titulo,
            mLugar : this.props.data.lugar,
            mPeriodo : this.props.data.periodo,
            mTipo : this.props.data.tipo        
        }        

    }

    render(){

        return(
               
                    <div class="formacion_card_element">
                        <div class="formacion_card_titulo">                  
                            <p class="h4 formacion_card_periodo"> {this.state.mPeriodo}</p>
                            <p class="h4 formacion_card_tipo"> {this.state.mTipo}</p>
                        </div>
                        <div class="formacion_card_contenido">                        
                            <p><span class="formacion_card_contenido_lugar">{this.state.mLugar}</span></p>
                            <p><span class="formacion_card_contenido_titulo">{this.state.mTitulo}</span></p>                        
                        </div>
                    </div>
             
        );
    }

}

export default FormacionCard ;