import React, {Component} from 'react';
import icono from '@material-ui/icons/RecordVoiceOver';
import SvgIcon from '@material-ui/core/SvgIcon';
import IdiomasDetalle from '../idiomas/IdiomasDetalle';
import {Table} from 'react-bootstrap';
import ContentDetalle from './ContentDetalle';

class ContentHome extends Component{
    constructor(props){
        super(props);
        this.state = {            
            mError : null,
            mLoaded : false,
            mList:[],
            mName: this.props.name,
            mIcon: this.props.icon,
            mColumns: this.props.columns,
            mSource: this.props.source
        }
    console.log("llego hasa aca");
    }

    render(){

        if(this.state.mError){
            return (
                <div>
                    Error:{this.state.mError.message};
                </div>
            )
        }
        else if(!this.state.mLoaded){
            return(
                <div>
                    <label>Cargando</label>
                </div>
            )
        }else{
            return(
                <div>                
                    <p class="h4"><SvgIcon component={this.state.mIcon}/>{this.state.mName}</p>            
                    <div>
                        <ul>
                            <Table striped bordered size="sm" width="10px" header>
                                <thead>
                                        <tr>
                                            {this.state.mColumns.map(data => <th>{data}</th>)}
                                        </tr>
                                </thead>
                                <tbody>
                                    {this.state.mList.map(data => <ContentDetalle data={data}/>)}
                                </tbody>
                            </Table> 
                        </ul>
                    </div>
                </div> 
            )
        }
    }
    
    componentDidMount(){
        
        var listLocal = [];
        var data = require('../../data/'+this.state.mSource);
        listLocal = data.rows;
        this.setState({
            mLoaded: true,
            mList: listLocal
        })
    
    }


}

export default ContentHome;