class Home extends Component {
  constructor(props) {
    super(props);
    this.state = {
      homeError: null,
      homeProductosListLoaded: false,
      homeProductos: [],
      homePage: "home",
      homeMenuBarStrings: ["Inicio", "Sobre mí", "Formación", "Experiencia", "Conocimientos", "Contacto"]
    };

    this.handleSelect = this.handleSelect.bind(this);
    this.handleClick = this.handleClick.bind(this);
    this.esIE = this.esIE.bind(this);

    if (!('scrollBehavior' in document.documentElement.style)) {
      //safari does not support smooth scroll
        (async () => {
          const {default: smoothScroll} = await import(
            /* webpackChunkName: 'polyfill-modern' */
            'smoothscroll-polyfill'
            )
          smoothScroll.polyfill()
        })()
      }

  }

  handleSelect(key){    
    this.setState({homePage:key});
    /*alert(`Selected: ${this.state.homePage}`);*/
    /*$('body').scrollspy({target: "section_xp", offset:50});*/
    /*window.scroll({top:1000, left:0, behavior: 'smooth'});*/
    /*document.querySelector('header').scrollIntoView({ behavior: 'smooth' });*/
    /*var s = "[id=" + key + "]";
    alert(s);
    var d = document.querySelector(s);
    alert(`${key}`);
    alert(d);
    d.scrollTo({behavior: 'smooth'});*/
  }

  handleClick(event){
    
    event.preventDefault();
    console.log(event);
    let parent = event.target.parentElement;
    console.log(parent);
    let section = '';

switch(parent.id){
    case 'section_inicio_navlink': {
        section = 'section_inicio';
        break;
    }
    case 'section_about_navlink': {
        section = 'section_about';
        break;
    }
    case 'section_formacion_navlink': {
        section = 'section_formacion';
        break;
    }
    case 'section_xp_navlink': {
        section = 'section_xp';
        break;
        }
    case 'section_skills_navlink': {
          section = 'section_skills';
          break;
        }
    default : {
          section = 'section_inicio';
          break;
        }
    }
    
    let targetSectionQuery = "[id=" + section + "]";
    console.log(targetSectionQuery);
    
    let targetSectionElement = document.querySelector(targetSectionQuery);
    console.log(targetSectionElement);
    

    if(this.esIE()){
      targetSectionElement.scrollIntoView();
      }else{
      let rect = targetSectionElement.getBoundingClientRect();            
      let dic = {behavior: 'smooth'};
      targetSectionElement.scrollIntoView(dic);
  }

/*    
    let s = "[id=" + this.state.homePage + "]";
    let e = document.querySelector(s);
    if (e != null){    
      //console.log(e);
      if(this.esIE()){
          e.scrollIntoView();
      }else{
          let rect = e.getBoundingClientRect();
        //  alert("X: " + rect.x + " Y: " + rect.y);
          let dic = {behavior: 'smooth'};
          e.scrollIntoView(dic);
      }
    }
    */
   
  }

  esIE() {
    const userAgent = window.navigator.userAgent;
    const msie = userAgent.indexOf('MSIE ');
    const trident = userAgent.indexOf('Trident/'); //IE 11

    return (msie > 0 || trident > 0);
}


  componentDidMount(){

    /*var d = document.querySelectorAll("[id^='section']");    
    console.log("Lista:");
    console.log(d);   */

  }


  render() {
    
 

    return (
          <div>

                      <div>
                          <div class="content_td_menubar">
                                    <div class="content_menubar" id="content_active">
                                        <Nav variant="pills" defaultActiveKey="/home" className="flex-column" onSelect={this.handleSelect}>
                                        <Nav.Item class="content_menubar_item1">
                                              <Nav.Link id='section_inicio_navlink' eventKey="section_inicio" href="#section_inicio" onClick={this.handleClick}>
                                                <p class="h6">{this.state.homeMenuBarStrings[0]}</p>
                                              </Nav.Link>
                                          </Nav.Item>
                                          <br/>
                                          <Nav.Item>
                                              <Nav.Link id='section_about_navlink' eventKey="section_about" href="#section_about" onClick={this.handleClick}>
                                                <p class="h6">{this.state.homeMenuBarStrings[1]}</p>
                                              </Nav.Link>
                                          </Nav.Item>
                                          <br/>
                                          <Nav.Item>
                                            <Nav.Link id='section_formacion_navlink'eventKey="section_formacion" href="#section_formacion" onClick={this.handleClick}>
                                            <p class="h6">{this.state.homeMenuBarStrings[2]}</p>
                                              </Nav.Link>
                                          </Nav.Item>
                                          <br/>
                                          <Nav.Item>
                                            <Nav.Link id='section_xp_navlink' eventKey="section_xp" href="#section_xp" onClick={this.handleClick}>
                                            <p class="h6">{this.state.homeMenuBarStrings[3]}</p>
                                            </Nav.Link>
                                          </Nav.Item>
                                          <br/>
                                          <Nav.Item>
                                            <Nav.Link id='section_skills_navlink' eventKey="section_skills" href="#section_skills" onClick={this.handleClick}>
                                            <p class="h6">{this.state.homeMenuBarStrings[4]}</p>
                                            </Nav.Link>
                                          </Nav.Item>
                                          <br/>
                                          <Nav.Item>
                                            <Nav.Link id='section_contacto_navlink' eventKey="section_contacto" href="#section_contacto" smooth="true">
                                            <p class="h6">{this.state.homeMenuBarStrings[5]}</p>
                                            </Nav.Link>
                                          </Nav.Item>
                                        </Nav>
                                      </div>
                            </div>



                           <div class="content_td_active">
                                      <div class="content_active">
                                          <MainName/>
                                          <AboutHome/>
                                          <FormacionHome/>            
                                          <XpHome/>
                                          
                                          <SkillsHome/>
                                  </div>

                          </div>

                      </div>
                  
                      </div>        
    );
  }



}

export default Home;

