import { Tabs, Tab } from 'react-bootstrap';
import React, { Component } from "react";

//Elementos
import ContentHome from './ContentHome.js';

//Iconos
import icono_formacion from '@material-ui/icons/School';
import icono_idiomas from '@material-ui/icons/RecordVoiceOver';
import icono_cursos from '@material-ui/icons/MenuBook';
import icono_skills from '@material-ui/icons/Stars';
import icono_xp from '@material-ui/icons/Assignment';

class ContentTab extends Component {
    constructor(props){
        super(props);
        this.state = {            
        }        
    }

    render(){
        let items = ["Formacion Profesional", "Idiomas", "Cursos", "Conocimientos", "Experiencia Laboral"];

        let ids = [0,1,2,3,4];

        console.log(ids);

        let keys = ["formacion", "idiomas", "cursos", "skills", "xp"];

        let iconos = [icono_formacion,icono_idiomas, icono_cursos,icono_skills,icono_xp];

        let columnas = [
                ["Título", "Lugar","Tipo", "Año"],
                ["Título", "Idioma", "Lugar", "Año"],
                ["Curso", "Lugar","Nombre","Detalle","Año"],
                ["Nombre", "Detalle"],
                ["Lugar", "Año","Rol","Detalle"]
                ];
        let origenes = ['formacion.json','idiomas.json','cursos.json','conocimientos.json','xp.json'];

        return (
                <Tabs defaultActiveKey={"key"+keys[0]}  >
                    {ids.map( (id) =>                                      
                        <Tab eventKey={"key"+keys[id]} title={items[id]}>
                            <div id={"content_"+keys[id]}>
                                <ContentHome name={" "+ items[id]} icon={iconos[id]} columns={columnas[id]} source={origenes[id]} />
                            </div>
                        </Tab>
                    )}                    
                </Tabs>
               
          );
        }

}

export default ContentTab;