import React, {Component} from 'react';
import FormacionDetalle from '../../page/formacion/FormacionDetalle.js';
import FormacionCard from '../../page/formacion/FormacionCard';
import icono from '@material-ui/icons/School';
import SvgIcon from '@material-ui/core/SvgIcon';
import { CardDeck, Col, Container,  Row } from 'react-bootstrap';
import {Table} from 'react-bootstrap';


class FormacionHome extends Component{
    constructor(props){
        super(props);
        this.state = {
            mError : null,
            mLoaded : false,
            mList:[]
        }
    }

    
    render(){

        if(this.state.mError){
            return (
                <div class="content_active_element content_formacion">
                    Error:{this.state.mError.message};
                </div>
            )
        }
        else if(!this.state.mLoaded){
            return(
                <div  class="content_active_element content_formacion">
                    <label>Cargando</label>
                </div>
            )
        }else{
            return(
                <div id="section_formacion" class="content_active_element content_formacion" >                    
                    <h2 class="content_active_element_title">Formación</h2>                
                    <br/>
                    <br/>

                    <div>
                        <ul>
                            {this.state.mList.map( data => <FormacionDetalle data={data} /> )}            
                            <br/>
                        </ul>

                    </div>

                   
                </div> 
            )
        }
    }

    componentDidMount(){
        
        var mListLocal = [];
        var data = require('../../data/formacion2.json');
        mListLocal = data.formacion;
        this.setState({
            mLoaded: true,
            mList: mListLocal
        })

        
    var d = document.querySelectorAll("[id^='section']");
    
    }
}
export default FormacionHome

