import React, {Component} from 'react';
import XpDetalle from '../../page/xp/XpDetalle.js';
import icono from '@material-ui/icons/Stars';
import SvgIcon from '@material-ui/core/SvgIcon';
import './Xp.css';

class XpHome extends Component{    
    constructor(props){
        super(props); 
        this.state = {
            mError : null,
            mLoaded : false,
            mList:[],
            mIsOvering:false,
            mIsTimerRunning:false,
            mCurrentElement:null
        }
        
        const isOvering = false;

        this.crearDetalle = this.crearDetalle.bind(this);
        this.handleMouseOver = this.handleMouseOver.bind(this);
        this.handleMouseOut = this.handleMouseOut.bind(this);

    }


    handleMouseOut(event){

        if (this.state.mIsOvering === true){

            if (this.state.mIsTimerRunning === false){

                    this.setState({mIsTimerRunning:true});

                    let timer = setTimeout(
                        () => {
                            this.setState({mIsTimerRunning:false});
                            this.setState({mIsOvering : false});
                            console.log("timer finalizo");
                        }
                        ,500);
                        console.log("timer empezo");
            }else{
                console.log("Hay un timer corriendo");
            }
        }else{
            console.log("No esta overing");
        }
      }


    handleMouseOver(event){
    
        //event.preventDefault();*/
        
       let current = event.target.parentElement.parentElement;       

       if ( current.className.includes('content_xp_li_par')){
       //   console.log("Elemento: "+current.className);
        

        if(this.state.mCurrentElement !== current ){

            if(this.state.mIsOvering===false){

                    this.setState({mIsOvering : true, mCurrentElement: current});                                               

                    if(this.esIE()){
                        current.scrollIntoView();
                    }else{
                        let rect = current.getBoundingClientRect();            
                        let dic = {behavior: 'smooth', block:'center'};
                        current.scrollIntoView(dic);
                    }
                    
                    let timer = setTimeout(
                        () => {
                            //this.setState({mIsTimerRunning:false});
                            this.setState({mIsOvering : false});
                            console.log("timer finalizo");
                        }
                        ,500);
                    }

                }

            }
      }



      esIE() {
        const userAgent = window.navigator.userAgent;
        const msie = userAgent.indexOf('MSIE ');
        const trident = userAgent.indexOf('Trident/'); //IE 11
    
        return (msie > 0 || trident > 0);
    }














    render(){

        let myData = {
            empresa: "Pepe", periodo: "2000-2001", rol: "Hesa", descripcion: "SOIDOIDIAIHODSAIHOSADHOIDSAHOIDAHOIDSAHOISDAHOISDAHOISD"
        }

        if(this.state.mError){
            return (
                <div class="content_active_element content_xp">
                    Error:{this.state.mError.message};
                </div>
            )
        }
        else if(!this.state.mLoaded){
            return(
                <div class="content_active_element content_xp" >
                    <label>Cargando</label>
                </div>
            )
        }else{
            return(
                <div id="section_xp" class="content_active_element content_xp" >                    
                   <h2 class="content_active_element_title">Experiencia</h2> 
                   <br/>
                   <br/>                    
                    <div class="content_xp_container">
                        <ul>
                            
                        <li class="li content_xp_li_inicio">
                            <div class="content_xp_timeline_inicio">
                                <div class="content_xp_timeline_dot_inicio"></div>                          
                            </div> 

                                                          
                            </li>  

                            {this.state.mList.map(
                                        this.crearDetalle
                                        )}            


                            <li class="li content_xp_li_ultimo">
                                <div class="content_xp_timeline_ultimo">
                                    <h6><span>Actualidad</span></h6>                                                                        
                                </div> 
                            </li>  

                        </ul>
                        
                    </div>
                </div> 
            )
        }
    }

    crearDetalle(data, index){

//        console.log("CREAR DETALLE");
//        console.log(data);
//        console.log(index);
        
            return (
                   
                            <XpDetalle data={data} par={true} indice={index} onMouseHoverHandler={this.handleMouseOver} onMouseOutHandler={this.handleMouseOut}/> 
                                        );
                    
        
    }



    componentDidMount(){
        
        var listLocal = [];
        var data = require('../../data/xp2.json');
        listLocal = data.roles;
        this.setState({
            mLoaded: true,
            mList: listLocal
        })
        console.log("XPHOME");
        console.log(listLocal);
    
    }


}

export default XpHome;

