import React, {Component} from 'react';
import { Col, Container,  Row } from 'react-bootstrap';

class SkillsBar extends Component{
    constructor(props){
        super(props);
        this.state = {
            mValue : this.props.data.valor,
            mText : this.props.data.nombre
    };
    //console.log(this.props.data);
    }

    render(){

        const styleContainer = {
            height: '10px',
            width: '100%',
            backgroundColor: '#c7c7c7', 
            borderRadius: 1,
            marginTop: 0,
            marginLeft: 20,
            marginRight: 20,
            marginBottom: 20,
            boxShadow: '-1px 3px 3px #a3a3a3'
            };

        const styleFiller = {
                height: '100%',
                width: `${this.props.data.valor}%`,
                //backgroundColor: '#006600',
                /*backgroundColor: '#001c68',*/
                backgroundColor: 'black',
                borderRadius: 'inherit',
                textAlign: 'left'
                //boxShadow: '0px 3px 3px #a3a3a3'
                };

        const styleTextContainer = {
            width:'100%',
            textAlign: 'left',
            paddingLeft: '15px',            
        }

        const  styleText = {
            width: '100%',            
            padding: '5px',
            textAlign: 'left',            
            color: '#135997',
            fontWeight:  'normal',
            letterSpacing: 3 ,
            fontSize: 'medium'
            };
        return(
            <div>
                <div style={styleTextContainer}>
                    <span style={styleText}>{this.state.mText}</span>            
                </div>
                <div style={styleContainer}>
                    <div style={styleFiller}>                    
                    </div>
                </div>
            </div>

        );
    }   
}

export default SkillsBar;