import React, {Component} from 'react';
import './AboutHome.css';
import foto from "../../img/profile3.jpg";

class AboutHome extends Component{

    constructor(props){
        super(props);        

    }

    render(){
            return(                
                <div id="section_about" class="content_active_element about_home">                    
  
                    <div>
                        <div>
                            <h2 class="content_active_element_title about_home_title">
                                Sobre mí
                            </h2>
                        </div>
                        

                    </div>
                    <br/>
                    <br/>
                    <div>
                    <table>
                            <tr>
                                <td>
                                    <div>
                                        <img class="about_home_image" src={foto} alt="Juan Pablo" />
                                    </div>
                                </td>
                             
                            <td>
                                <div>
                                    <blockquote class="blockquote text-left">                                                                         
                                    <p class="mb-1">Mi nombre es Juan Pablo. Soy argentino.</p>                        
                                    <p class="mb-1">Nací, me crié y viví siempre en Buenos Aires.</p>                        
                                    <p class="mb-1">Desde pequeño me sentí intrigado por el mundo de la computación.</p>                        
                                    <p class="mb-1">Como les ha ocurrido a muchos, al principio solo era un pasatiempo.</p>                        
                                    <p class="mb-1">Pero ademés me fui interesando en la programación y el desarrollo de software.</p>                        
                                    <p class="mb-1">Es por eso que mis estudios siguieron este camino.</p>                        
                                    <p class="mb-1">Desde entones he tenido la suerte de dedicarme a esta profesion tan apacionante,</p>                        
                                    <p class="mb-1">como desafiante y dinámica.</p>                        
                                    </blockquote>
                                </div>
                             </td>
                            </tr>
                    </table>
                    </div>
                </div> 
            );
        }
    

}

export default AboutHome;

