import React, {Component} from 'react';
import { Col, Container,  Row } from 'react-bootstrap';

class IdiomasDetalle extends Component{
    constructor(props){
        super(props);
        this.state = {
            mLugar : this.props.data[1],
            mIdioma : this.props.data[2],
            mTitulo : this.props.data[3],
            mYear : this.props.data[4]
        }
    }
       
    render(){

        return(
            <tr>
                <td>{this.state.mTitulo}</td>
                <td>{this.state.mLugar}</td>
                <td>{this.state.mIdioma}</td>
                <td>{this.state.mYear}</td>
            </tr>                
        );
    }

}

export default IdiomasDetalle;